export default {
	data() {
		return {
			mydata:{},//认证状态对象
			type:-1
		}
	},
	methods: {
		// 重新认证
		reset:function(){
			this.type=-1
		},
		// 初始化数据
		myajax:function(){
			var that=this
			uni.showLoading({
			    title: '加载中',
				mask:true
			});
			uni.request({
			    url: 'https://www.example.com/request', //仅为示例，并非真实接口地址。
			    data: {
			        text: 'uni.request'
			    },
			    success: (res) => {
			        console.log(res.data);
					that.mydata=res.data
			    }
			});
			setTimeout(function() {
				uni.hideLoading()
			}, 1000);
		},
		opennew:function(id){
			uni.navigateTo({
				url: '../'+id+'/'+id
			});
			
		}
	},
	onShow:function(){
		this.myajax()
	}
}
